import React from "react";
import "./style.css";
import CreditCard from "../../CreditCard/index"

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value1: "",
            value2: "",
            value3: "",
            value4: "",
            currentIndex: 0,
            disableInput: true
        };
        this.input1 = React.createRef();
        this.input2 = React.createRef();
        this.input3 = React.createRef();
        this.input4 = React.createRef();
    }


    //Function to validate Clipboard Copied item and place it in 4 input fields
    handlePaste = (event) => {

        //Function made to give an array of copied item 
        Array.prototype.mapNumbers = function (callback) {
            const resultArray = [];
            for (let index = 0; index < 16; index += 4) {
                let value = this[index] + this[index + 1] + this[index + 2] + this[index + 3];
                resultArray.push(callback(value));
            }
            return resultArray;
        }

        event.clipboardData.items[0].getAsString(text => {
            if ((text.split("").length == 16) && (/^\d+$/.test(text))) {  //checking length and if alphabet presence
                const code = text.split("").mapNumbers((value) => { return value });
                this.setState({ value1: code[0], value2: code[1], value3: code[2], value4: code[3] });//placing Values in  input field
                this.input4.current.focus();
            }
            else {
                this.setState({ value1: '', value2: '', value3: '', value4: '' });
                this.input1.current.focus();
                alert("Please enter 16 digit correct pin");
            }
        })
    }


    //Function to update state with change in input fields
    onChange = (e, index) => {
        const re = /^[0-9\b]+$/;
        if (e.target.value === '' || re.test(e.target.value)) {
            this.setState({
                [e.target.name]: e.target.value,
                currentIndex: index,
                disableInput: false
            });
        }

        //Function to move focus on deletion
        if (e.target.value == '') {
            const refs = [this.input1, this.input2, this.input3, this.input4];
            if (index != 0) { refs[index - 1].current.focus() }
            else {
                refs[index].current.focus();
                this.setState({ disableInput: true });
            }
        }
    };

    componentDidUpdate() {
        const refs = [this.input1, this.input2, this.input3, this.input4];
        const { currentIndex } = this.state;
        const currentRef = refs[currentIndex].current;

        //Function to move focus to next input field
        if (currentRef.value.length === currentRef.maxLength && currentIndex < refs.length - 1) {
            refs[currentIndex + 1].current.focus();
        }
    }

    render() {
        return (
            //Credit Card Component 
            <CreditCard
                {...this.state}
                handlePaste={this.handlePaste}
                onChange={this.onChange}
                input1={this.input1}
                input2={this.input2}
                input3={this.input3}
                input4={this.input4}
            />
        );
    }
}

export default Home;



