import React from "react";

import "./style.css";
import Chip from "../../../../chip.png"


class CreditCardLayout extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="background">
                <div className="card-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="bank-name">Bank Name</div>
                        </div>
                        <div className="col-md-12">
                            <div className="chip"><img className="chip-img" src={Chip} /></div>
                        </div>
                        <div className="col-md-12">
                            {this.props.children}
                        </div>

                        <div className="col-md-12">
                            <div className="card-exp">Exp : 10/23</div>
                        </div>

                        <div className="col-md-12">
                            <div className="card-holder">Card Holder</div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default CreditCardLayout;