import React from "react";
import "./style.css";
import CreditCardLayout from "../../Layout/CreditCardLayout/index"
import InputPin from "../../Layout/InputPin/index"

class CreditCard extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <CreditCardLayout >
                <InputPin {...this.props} />
            </CreditCardLayout>
        );
    }
}

export default CreditCard;