import React from "react";
import "./style.css";

class InputPin extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div class="number multifield" onPaste={this.props.handlePaste}>
                <input className="customInput" type="text" maxlength="4" pattern="\d+"
                    value={this.props.value1}
                    onChange={e => this.props.onChange(e, 0)}
                    ref={this.props.input1} name="value1" autoFocus />

                <input className="customInput" type="text" maxlength="4" pattern="\d+"
                    value={this.props.value2}
                    onChange={e => this.props.onChange(e, 1)}
                    ref={this.props.input2} name="value2" disabled={this.props.disableInput ? "disabled" : ""} />

                <input className="customInput" type="text" maxlength="4" pattern="\d+"
                    value={this.props.value3}
                    onChange={e => this.props.onChange(e, 2)}
                    ref={this.props.input3} name="value3" disabled={this.props.disableInput ? "disabled" : ""} />

                <input className="customInput" type="text" maxlength="4" pattern="\d+"
                    value={this.props.value4}
                    onChange={e => this.props.onChange(e, 3)}
                    ref={this.props.input4} name="value4" disabled={this.props.disableInput ? "disabled" : ""} />
            </div>
        );
    }
}

export default InputPin;